import './App.css';
import Experience from './components/Experience';
import Title from './components/Title';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <BrowserRouter>
          <Switch>
          <Route exact path="/">
            <Title/>
            <Experience/>
          </Route>
          <Route path="/test">
            <Experience/>
          </Route>
        </Switch>
        </BrowserRouter>
      </header>
    </div>
  );
}

export default App;
